import React, { useEffect, useState } from "react";
import "./group.css";
const useAudio = (url) => {
  const [audio] = useState(new Audio(url));
  const [playing, setPlaying] = useState(false);
  // const setStyle = (playing) => {
  //   return {style: playing?};
  // };
  const toggle = () => {
    setPlaying(!playing);
    // setStyle(playing);
  };
  const playAudio = (audio) => {
    try {
      if (audio.readyState == HTMLMediaElement.HAVE_ENOUGH_DATA) audio.play();
    } catch (e) {
      console.log("audio isn't available because: " + e.message);
    }
  };
  useEffect(() => {
    playing ? playAudio(audio) : audio.pause();
  }, [playing]);
  useEffect(() => {
    audio.addEventListener("ended", () => {
      setPlaying(false);
    });
    return () => {
      audio.removeEventListener("ended", () => setPlaying(false));
    };
  }, []);
  return [playing, toggle];
};
export default function Player(props) {
  const { url, audioName, icon: Icon } = props;
  const [playing, toggle] = useAudio(url);
  return (
    <div>
      <h3>{audioName}</h3>

      <button onClick={toggle}>
        <Icon fontSize="large" />
      </button>
    </div>
  );
}
