import React from "react";
import { Col, Divider, Row } from "antd";
import Player from "./Player.js";
import "./group.css";

const public_path = process.env.PUBLIC_URL;

const createIconArray = (category_path, audios) => {
  const arr = [];
  for (let i = 0; i < audios.length; i++) {
    arr.push(
      <Col key={i + 1} className="gutter-row" span={3}>
        <div className="icon-wrapper">
          <Player
            url={public_path + category_path + audios[i].filename}
            audioName={audios[i].label}
            icon={audios[i].icon}
          ></Player>
        </div>
      </Col>
    );
  }
  return arr;
};

export default function SoundGroup({ group_label, category_path, audios }) {
  return (
    <div className="group-wrapper">
      <Divider orientation="left">{group_label}</Divider>
      <Row justify="space-around" align="middle">
        {createIconArray(category_path, audios)}
      </Row>
    </div>
  );
}
